<template>
  <div class="goods_info">
    <div class="count_top header">
      <img src="../../assets/shopImage/left_icon.png" alt="" @click="back">
      <span>商品详情</span>
    </div>
    <mt-swipe :show-indicators="false" :auto="3000" class="banner">
      <mt-swipe-item v-for="(item,key) in carousel" :key="key" >
        <div class="good_img">
          <img :src="item" alt="">
        </div>
      </mt-swipe-item>
    </mt-swipe>
    <section class="good_info">
      <p class="good_desc">{{goodsInfo.good_name}}</p>
      <p class="good_price">¥ {{goodsInfo.good_price}}</p>
      <p>
        <span>销量: {{goodsInfo.xiaoliang}}</span>
        <span style="margin-left: 0.2rem">库存: {{goodsInfo.number}}</span>
        <span style="float: right">运费: {{goodsInfo.yunfei}}</span>
      </p>
    </section>
    <p class="line"></p>
    <section class="good_desc" v-html="goodsInfo.good_desc"></section>
    <footer class="good_foot" @click="convert">
      立即购买
    </footer>
    <div class="wrapper" v-if="flag" @click.stop="flag = !flag">
      <div class="wrap">
        <div class="wrap_price">
          <img :src="carousel[0]" alt="" >
          <section>
            <h2>¥ {{goodsInfo.good_price}}</h2>
            <span>库存 {{goodsInfo.number}}</span>
          </section>
        </div>
        <section class="wrap_num">
          <p>购买数量</p>
          <p class="wrap_add">
            <span @click.stop="cut">-</span><span>{{num}}</span><span @click.stop="add">+</span>
          </p>
        </section>
      </div>
      <section class="wrap_btn" @click.stop="sure">确定</section>
    </div>
  </div>
</template>
<script>
  import { Toast} from 'mint-ui';
  export default {
    data(){
      return {
        flag: false,
        goodsInfo: {},
        carousel: [],
        num:1,
        //商品id
        id: '',
      }
    },
    methods:{
      back(){
        this.$router.go(-1);
      },
      convert(){
        //1 购物车 2 购买
        this.flag = true;
      },
      sure(){
        if (this.num !== 0 && this.num <= this.goodsInfo.number){
          this.buyRight(this.goodsInfo.id);
        }
      },
      //数量减
      cut(){
        if (this.num !== 0){
          this.num--;
        }
      },
      add(){
        this.num++;
      },
      //立刻购买
      buyRight(id){
        let url = `/countBuy/${id}/${this.num}`;
        this.$router.push(url)
      },
    },
    mounted(){
      this.id = this.$route.params.id;
      this.post('home/Shop/getShopById', {
        good_id: this.id,
        is_buy: 1,
        type: 1,
      }).then(res => {
        if (res.data.code === 10000) {
          this.goodsInfo = JSON.parse(JSON.stringify(res.data.info));
          this.carousel = res.data.info.pic_url;
          this.carousel = this.carousel.map(item => {
            return item = this.imageURL(item);
          });
        }
      })
    }
  }
</script>
<style scoped>
  @import '../../../static/header.css';
  .goods_info {
    height: calc(100vh - 0.9rem);
    overflow-y: auto;
  }
  .banner{
    height: 5rem;
    background-color: #F2F2F2;
  }
  .good_img {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 5rem;
    overflow: hidden;
  }
  .good_img img {
    position: absolute;
    top: 0;
    width: 100%;
    min-height: 100%;
  }
  .good_info {
    padding: 0.3rem;
  }
  .good_info p {
    font-size: 0.28rem;
  }
  .good_desc {
    color: #111111;
    width: 95vw;
    overflow: hidden;
  }
  .good_price {
    color: #ff6f00;
    margin: 0.3rem 0 0.2rem 0;
  }
  .good_info p span{
    font-size: 0.24rem;
    color: #666666;
  }
  .line {
    height: 0.2rem;
    background-color: #F2F2F2;
  }
  .good_foot {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: 1rem;
    display: flex;
    align-items: center;
    font-size: 0.32rem;
    color: #FFFFFF;
    justify-content: center;
    background-color: #ff6f00;
  }
  .wrapper{
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0,0,0, 0.4);
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
  @keyframes  myanimation {
    from {
      transform: translateY(4rem);
    }
    to {
      transform: translateY(0);
    }
  }
  .wrap {
    padding: 0.3rem 0.4rem;
    background-color: #FFFFFF;
    animation: myanimation 0.5s ease;
  }
  .wrap_price {
    display: flex;
  }
  .wrap_price img {
    width: 1.61rem;
    height: 1.61rem;
    margin-top: -0.8rem;
  }
  .wrap_price section {
    margin-left: 0.3rem;
  }
  .wrap_price h2 {
    font-size: 0.3rem;
    color: #ff6f00;
    margin-bottom: 0.2rem;
  }
  .wrap_price section span {
    font-size: 0.24rem;
    color: #444444;
  }
  .wrap_num {
    margin: 0.6rem 0;
    display: flex;
    justify-content: space-between;
  }
  .wrap_num>p:first-child {
    font-size: 0.26rem;
    color: #444444;
  }
  .wrap_add span {
    display: inline-block;
    width: 0.66rem;
    height: 0.45rem;
    font-size: 0.32rem;
    color: #666666;
    text-align: center;
    border: 1px solid #aeaeae;
    line-height: 0.37rem;
  }
  .wrap_add span:first-child {
    border-top-left-radius: 0.2rem;
    border-bottom-left-radius: 0.2rem;
    border-right: none;
  }
  .wrap_add span:last-child{
    border-top-right-radius: 0.2rem;
    border-bottom-right-radius: 0.2rem;
    border-left: none;
  }
  .wrap_btn {
    height: 0.88rem;
    text-align: center;
    background-color: #ff6f00;
    color: #FFFFFF;
    font-size: 0.36rem;
    line-height: 0.88rem;
    cursor: pointer;
  }
</style>



